.header{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}

.logo {
    width: 130px;
    padding: 5px;
    /* max-width: 100%; */
    height: auto;
    object-fit: contain;
}

/* .nav-items{

} */
.nav-items>ul{
    font-size: 24px;
    display: flex;
    list-style: none;
    
}

.nav-items>ul>li{
    padding: 10px;
    margin: 10px;
    
    
}

.res-container{
    display: flex;
    flex-wrap: wrap;
    
}

.res-card{
    margin: 5px;
    padding: 5px;
    width: 200px;
    height: 300px;
    /* border: 1px solid black; */

}

.res-card:hover{
    border: 1px solid black;
    cursor:pointer
}
.search{
    padding:10px;
}

.res-logo{
    padding: auto;
    width: 100%;
}